<script>
export default {
  name: 'App',
  data() {
    return {
      currentImageIndex: 0,
      bannerImages: [
        require('@/assets/banner1.jpg'),
        require('@/assets/banner2.jpg'),
        require('@/assets/banner3.jpg'),
        require('@/assets/banner4.jpg')
      ],
      carouselInterval: null,
      logoImage: require('@/assets/logo.png'),
      filtroActivo: 'todos',
      headerVisible: true,
      lastScrollPosition: 0,
      scrollThreshold: 100, // los pixeles que hay que bajar para esconder el header
      
      talleresEBI: [
        { id: 1, nombre: 'Volley', responsable: 'Ignacio Cugini', descripcion: 'Desarrolla habilidades de equipo y coordinaci√≥n motriz.', categoria: 'EBI' },
        { id: 2, nombre: 'Basquet', responsable: 'Mateo Pose', descripcion: 'Fomenta el trabajo en equipo y la agilidad f√≠sica.', categoria: 'EBI' },
        { id: 3, nombre: 'F√∫tbol', responsable: 'Carlos Vega', descripcion: 'Promueve valores deportivos y la actividad f√≠sica.', categoria: 'EBI' },
        { id: 4, nombre: 'Tenis de Mesa', responsable: 'Caro Gonz√°lez', descripcion: 'Mejora reflejos y concentraci√≥n mediante el juego.', categoria: 'EBI' },
        { id: 5, nombre: 'Recreaci√≥n', responsable: 'Caro Gonz√°lez', descripcion: 'Actividades l√∫dicas para el desarrollo integral.', categoria: 'EBI' },
        { id: 6, nombre: 'Participaci√≥n juvenil', responsable: 'Patricio Ya√±ez', descripcion: 'Fomenta el liderazgo y la participaci√≥n activa.', categoria: 'EBI' },
        { id: 7, nombre: 'Audiovisual', responsable: 'Andr√©s Failache', descripcion: 'Explora la creaci√≥n de contenido visual y sonoro.', categoria: 'EBI' },
      ],

      talleresEBIEMS: [
        { id: 8, nombre: 'La letra', responsable: 'Federico Ivanier', descripcion: 'Taller de escritura creativa y expresi√≥n literaria.', categoria: 'EBI/EMS' },
        { id: 9, nombre: 'Teatro', responsable: 'Emiliano Duarte', coordinacion: 'Leonor Svarcas', descripcion: 'Desarrolla expresi√≥n corporal y habilidades esc√©nicas.', categoria: 'EBI/EMS' },
        { id: 10, nombre: 'Rob√≥tica y programaci√≥n', responsable: 'Adriana Silva', descripcion: 'Introducci√≥n a la tecnolog√≠a y pensamiento computacional.', categoria: 'EBI/EMS' }
      ],

      talleresEMS: [
        { id: 11, nombre: 'Ciencias de la computaci√≥n', responsable: 'Adriana Silva', descripcion: 'Profundiza en algoritmos y desarrollo de software.', categoria: 'EMS' },
        { id: 12, nombre: 'Salud y bienestar', responsable: 'Florencia Otonelli', descripcion: 'Promueve h√°bitos saludables y cuidado personal.', categoria: 'EMS' },
        { id: 13, nombre: 'Comunicaci√≥n audiovisual', responsable: 'Andr√©s Failache', descripcion: 'Producci√≥n avanzada de contenido multimedia.', categoria: 'EMS' },
        { id: 14, nombre: 'Recreaci√≥n y deporte', responsable: 'Carolina Gonz√°lez', descripcion: 'Integraci√≥n de actividad f√≠sica y desarrollo social.', categoria: 'EMS' }
      ]
    }
  },
  computed: {
    totalTalleres() {
      return this.talleresEBI.length + this.talleresEBIEMS.length + this.talleresEMS.length;
    },
    talleresFiltrados() {
      switch (this.filtroActivo) {
        case 'EBI':
          return this.talleresEBI;
        case 'EBI/EMS':
          return this.talleresEBIEMS;
        case 'EMS':
          return this.talleresEMS;
        default:
          return [...this.talleresEBI, ...this.talleresEBIEMS, ...this.talleresEMS];
      }
    }
  },
  mounted() {
    this.startCarousel();
    this.setupScrollHandler();
  },
  beforeUnmount() {
    this.stopCarousel();
    this.removeScrollHandler();
  },
  methods: {
    verDetalle(taller) {
      alert(`Redirigiendo a la p√°gina de: ${taller.nombre}\nResponsable: ${taller.responsable}\n\n${taller.descripcion}`);
    },
    startCarousel() {
      this.carouselInterval = setInterval(() => {
        this.nextImage();
      }, 4000);
    },
    stopCarousel() {
      if (this.carouselInterval) {
        clearInterval(this.carouselInterval);
      }
    },
    nextImage() {
      this.currentImageIndex = (this.currentImageIndex + 1) % this.bannerImages.length;
    },
    setCurrentImage(index) {
      this.currentImageIndex = index;
      this.stopCarousel();
      this.startCarousel();
    },
    getEmoji(nombreTaller) {
      const emojis = {
        'Volley': 'üèê',
        'Basquet': 'üèÄ',
        'F√∫tbol': '‚öΩ',
        'Tenis de Mesa': 'üèì',
        'Recreaci√≥n': 'üéØ',
        'Recreaci√≥n y deporte': '‚öΩ',
        
        'Teatro': 'üé≠',
        'Audiovisual': 'üé¨',
        'Comunicaci√≥n audiovisual': 'üé•',
        'La letra': 'üìù',
        
        'Rob√≥tica y programaci√≥n': 'ü§ñ',
        'Ciencias de la computaci√≥n': 'üíª',
        
        'Participaci√≥n juvenil': 'üë•',
        'Salud y bienestar': 'üíö'
      };
      
      return emojis[nombreTaller] || 'üéØ';
    },
    scrollToSection(sectionId) {
      const element = document.getElementById(sectionId);
      if (element) {
        element.scrollIntoView({ behavior: 'smooth' });
      }
    },
    mostrarCategoria(categoria) {
      return this.filtroActivo === 'todos' || this.filtroActivo === categoria;
    },
    setupScrollHandler() {
      window.addEventListener('scroll', this.onScroll);
    },
    removeScrollHandler() {
      window.removeEventListener('scroll', this.onScroll);
    },
    onScroll() {
      const currentScrollPosition = window.pageYOffset || document.documentElement.scrollTop;
      
      // si estamos arriba en la p√°gina, mostrar el header
      if (currentScrollPosition < 50) {
        this.headerVisible = true;
        this.lastScrollPosition = currentScrollPosition;
        return;
      }
      
      // ver la direcci√≥n del scroll
      const scrollingDown = currentScrollPosition > this.lastScrollPosition;
      
      // solo activar si se movio mas que el threshold
      if (Math.abs(currentScrollPosition - this.lastScrollPosition) > this.scrollThreshold) {
        this.headerVisible = !scrollingDown;
        this.lastScrollPosition = currentScrollPosition;
      }
    }
  }
}
</script>

